define(['aja', './template', './errorHandler'], function(aja, template, errorHandler) {
    'use strict';

    function addQueryParams(options, req) {
        return options.queryParameters ? req.queryString(options.queryParameters) : req;
    }

    function addBodyParams(options, req) {
        return options.bodyParameters ? req.body(options.bodyParameters) : req;
    }

    return {
        /**
         * Executes a request and processes the response as JSON.
         * @method get
         * @param {Object} options Specifies the details of the request.
         * @param {String} options.url The URL to request.
         * @param {Object} [options.queryParameters] The data to pass as GET parameters with the request.
         * @param {Object} [options.pathParameters] The data to use as {placeholders} in url.
         * @param {Function} [options.fail] The callback to execute when the request fails.
         * @param {Function} options.callback The callback to execute when the request completes.
         */
        get: function(options) {
            var url, req;
            options.pathParameters = options.pathParameters || {};
            options.pathParameters.contentType = 'json';
            options.pathParameters.protocol = options.pathParameters.protocol || 'https';

            url = template(options.url, options.pathParameters);

            req = aja().url(url);
            req = addQueryParams(options, req);
            req = errorHandler(options, req);
            req.on('success', options.callback).go();
        },

        /**
         * Executes a request and processes the response as JSON.
         * @method post
         * @param {Object} options Specifies the details of the request.
         * @param {String} options.url The URL to request.
         * @param {Object} [options.queryParameters] The data to pass as Url parameters with the request.
         * @param {Object} [options.pathParameters] The data to use as {placeholders} in url.
         * @param {Object} [options.bodyParameters] The data to pass to request body.
         * @param {Function} [options.fail] The callback to execute when the request fails.
         * @param {Function} options.callback The callback to execute when the request completes.
         */
        post: function(options) {
            var url, req;
            options.pathParameters = options.pathParameters || {};
            options.pathParameters.contentType = 'json';
            options.pathParameters.protocol = options.pathParameters.protocol || 'https';

            url = template(options.url, options.pathParameters);

            req = aja().url(url);
            req = addQueryParams(options, req);
            req = addBodyParams(options, req);
            req = errorHandler(options, req);
            req = req.method('POST');
            req.on('400', options.callback);
            req.on('success', options.callback).go();
        }
    };
});



