define(['../selector'], function(selector) {
    'use strict';
    var properties = [
        'address',
        'dist',
        'entryPoints',
        'position',
        'score',
        'poi',
        'type',
        'addressRanges',
        'boundingBox',
        'entityType',
        'viewport'
    ].join(', ');
    return function(json) {
        return selector(json, 'results', properties);
    };
});



