/**
 * Tile layer for Leaflet that use the
 * <a href="https://developer.tomtom.com/online-maps/online-maps-documentation/raster">raster online maps</a>
 * from TomTom.
 *
 * @example
 * ```js
 * var tomtomLayer = new L.TomTomLayer({key: "<Your API key>"});
 * ```
 *
 * @class TomTomLayer
 * @module Map
 * @namespace L
 * @extends L.TileLayer
 * @constructor
 * @param {Object} [options] It accepts all the options that
 *     <a href="http://leafletjs.com/reference-${leaflet.version}.html#tilelayer-options">Leaflet's TileLayer</a>
 *     allows.
 *     The additional options, which are TomTom specific, are described below.
 * @param {String} [options.key] API key that can be obtained from
 *     <a href="http://developer.tomtom.com">TomTom's developer portal</a>.
 *     When the key is passed on the map's initialization, it is set globally for all the services. It can be
 *     overwritten with the `tomtom.key()` method globally or per service with `tomtom.<service_name>.key()`
 *     functions. It is also possible to pass a specific key while working with instances of the services.
 * @param {"main"|"night"} [options.style="main"] The base map style to use
 * @param {"basic"|"hybrid"|"labels"} [options.layer="basic"] The base map's layer to use
 */
define(['leaflet', 'security', 'http', 'endpoints', 'display/layers/mapTheme', './animator', './optionsComplementor'],
    function(L, security, http, endpoints, mapTheme, animator, optionsComplementor) {
        'use strict';

        function use512Tiles(detectRetina, style, layer) {
            return detectRetina && L.Browser.retina && style === 'main' && ['basic', 'labels'].indexOf(layer) > -1;
        }

        function tileSizeEstimator(options) {
            return use512Tiles(options.detectRetina, options.style, options.layer) ? 512 : 256;
        }

        L.TomTomLayer = L.TileLayer.extend({
            options: {
                attribution: '',
                minZoom: 2,
                maxZoom: 18,
                subdomains: 'abcd',
                reuseTiles: true,
                detectRetina: false
            },
            name: 'raster',
            initialize: function(options) {
                var key, theme, urlParams;
                options = L.setOptions(this, options);
                if (options.bounds) {
                    options.bounds = L.latLngBounds(options.bounds);
                }
                security.setKey(options.key);
                key = security.getKey('services/display');
                options.accessToken = key;
                /* if 512 tiles are working uncomment the following line
                 this._url = http.protocol() + '://'+ endpoints.tileLayer +
                 '?tileSize={tileSize}' + (key ? '&key={accessToken}' : '');
                 */
                urlParams = (key ? 'key={accessToken}' : '') + (options.geopolView ? '&view={geopolView}' : '');
                this._url = http.protocol() + '://' + endpoints.tileLayer + (urlParams ? '?' + urlParams : '');
                theme = mapTheme.getTheme([options]);
                options.style = theme.style;
                options.layer = theme.layer;
                optionsComplementor.fillInSubdomainsOptions(options);
                optionsComplementor.fillInTiles512Options(options, tileSizeEstimator);
            },
            onAdd: function(map) {
                this._map = map;
                L.TileLayer.prototype.onAdd.call(this, map);
                if (this._animated) {
                    this.on('load', animator.loadAnimationFactory(this));
                }
                this._updateOpacity();
                this.bringToBack();
            },
            updateOptions: function(options) {
                var theme = mapTheme.getTheme([this.options, options]);
                this.options.style = theme.style;
                this.options.layer = theme.layer;
                this.redraw();
            }
        });

        L.tomTomLayer = function(options) {
            return new L.TomTomLayer(options);
        };

        return L.tomTomLayer;
    });



