define(['leaflet', 't', 'unitFormatConverter/unitFormatConverter'], function(L, T, units) {
    'use strict';

    var iconsMapping = {
            'Unknown': 'danger',
            'Accident': 'accident',
            'Fog': 'danger',
            'Dangerous Conditions': 'danger',
            'Rain': 'danger',
            'Ice': 'ice',
            'Jam': 'incident',
            'Lane Closed': 'laneclosed',
            'Road Closed': 'roadclosed',
            'Road Works': 'roadwork',
            'Wind': 'danger',
            'Flooding': 'danger',
            'Detour': 'danger',
            'Cluster': ''
        },
        iconTemplate = '<div class="traffic-icon"><div class="icon-circle-{{=incidentSeverity}}">' +
            '<div class="icon-{{=icon}}-white"></div></div></div>',
        shieldTemplate = '{{roadNumber}}<div class="road-shield">{{=roadNumber}}</div>{{/roadNumber}}',
        descriptionTemplate = '<div class="traffic-description"><div class="incident-category">' +
            '{{=incidentCategory}}</div>' +
            '<div class="incident-delay-length"' +
            '{{!delaySeconds}}{{!lengthMeters}} style="display:none;"{{/!lengthMeters}}{{/!delaySeconds}}>' +
            '{{delaySeconds}}<div class="incident-delay">{{=time}}</div>{{/delaySeconds}}' +
            '{{lengthMeters}}<div class="incident-length">{{=distance}}</div>{{/lengthMeters}}</div></div>',
        messageTemplate = new T('<div class="traffic-details">' + iconTemplate +
            shieldTemplate + descriptionTemplate + '</div>'),

        clusterPopupTemplate = new T('<div><div><b>Incidents in this area: {{=incidentsNo}}</b></div>' +
        '<hr/><div><b>{{=features.length}} most severe incidents</b>' +
        '<span style="color: lightgray">(Ordered by severity and delay)</span></div>' +
        '<hr/><div style="font-weight: bold">' +
        '<div class="cluster-popup-category-column">Category</div>' +
        '<div class="cluster-popup-length-column">Length</div>' +
        '<div class="cluster-popup-streets-column">Streets</div>' +
        '<div style="clear: both"></div><hr/>' +
        '</div>{{@features}}' +
        '<div><div class="cluster-popup-category-column">' +
        '<div class="traffic-icon">' +
        '<div class="icon-circle-{{=_val.properties.incidentSeverity}}">' +
        '<div class="icon-{{=_val.properties.icon}}-white"></div>' +
        '</div>' +
        '</div>' +
        '</div>' +
        '<div class="cluster-popup-length-column">' +
        '{{=_val.properties.formattedLength}}' +
        '</div>' +
        '<div class="cluster-popup-streets-column">' +
        '<div><span>From:</span>{{=_val.properties.from}}</div>' +
        '<div><span>To:</span>{{=_val.properties.to}}</div>' +
        '</div>' +
        '<div style="clear: both"></div>' +
        '<hr/>' +
        '</div>{{/@features}}' +
        '</div>');

    function trafficIncidentMessage(info, imperialDistance) {
        info.distance = units.formatDistance(info.lengthMeters, imperialDistance);
        info.time = units.formatTime(info.delaySeconds);
        info.incidentSeverity = info.incidentSeverity || 'unknown';
        info.icon = iconsMapping[info.incidentCategory];
        return messageTemplate.render(info);
    }

    function clusterClickHandler(bounds, latlng) {
        if (bounds) {
            return function() {
                this._map.fitBounds(bounds);
            };
        } else {
            return function() {
                this._map.setZoomAround(latlng, this._map.getZoom() + 2);
            };
        }
    }

    function compareDelays(p1, p2) {
        var d1 = p1.delaySeconds ? p1.delaySeconds : 0;
        var d2 = p2.delaySeconds ? p2.delaySeconds : 0;
        return d2 - d1;
    }

    function compareSeverity(p1, p2, severity) {
        var is1 = p1.incidentSeverity;
        var is2 = p2.incidentSeverity;
        if (is1 === severity) {
            return -1;
        } else if (is2 === severity) {
            return 1;
        }
        return 0;
    }

    function featuresComparator(f1, f2) {
        var p1 = f1.properties, p2 = f2.properties,
            severities, i, severityComparisonResult;

        if (p1.incidentSeverity === p2.incidentSeverity) {
            return compareDelays(p1, p2);
        }

        severities = ['major', 'moderate', 'minor', 'unknown', 'undefined'];
        for (i = 0; i < severities.length; ++i) {
            severityComparisonResult = compareSeverity(p1, p2, severities[i]);
            if (severityComparisonResult) {
                return severityComparisonResult;
            }
        }

        return 0;
    }

    function formatLength(lengthInMeters) {
        if (!lengthInMeters) {
            return '---';
        }

        if (lengthInMeters > 1000) {
            return (lengthInMeters / 1000).toPrecision(3) + ' km';
        }
        return lengthInMeters + ' m';
    }

    return function(feature, latlng, imperialDistance, options) {
        var iconOptions = {
                background: {
                    iconSize: L.Browser.retina ? [34, 34] : [20, 20],
                    iconAnchor: L.Browser.retina ? [17, 17] : [10, 10],
                    icon: ''
                },
                icon: {
                    iconSize: L.Browser.retina ? [34, 34] : [20, 20],
                    iconAnchor: [0, 0],
                    icon: ''
                },
                content: {
                    'iconSize': L.Browser.retina ? [34, 34] : [20, 20],
                    'iconAnchor': [0, 0],
                    'content': '',
                    'style': {
                        'color': '#fff',
                        'font-weight': 'bold',
                        'font-size': L.Browser.retina ? '18px' : '11px'
                    }
                },
                popupAnchor: L.Browser.retina ? [0, -8] : [0, -5]
            }, marker, featuresToShow, i;

        if (!feature || !feature.properties) {
            return null;
        }

        iconOptions.background.icon = 'icon-circle-' + (feature.properties.incidentSeverity || 'unknown');

        if (feature.properties.clusterSize) {
            iconOptions.icon = null;
            iconOptions.content.content = feature.properties.clusterSize < 100 ? feature.properties.clusterSize : '99+';
            marker = L.marker(latlng, {icon: L.svgIcon(iconOptions)});
            if (!options.createPopupForCluster) {
                marker.on('click', clusterClickHandler(feature.properties.clusterBounds, latlng));
            } else {
                featuresToShow = feature.properties.features.sort(featuresComparator).slice(0, 4);
                for (i = 0; i < featuresToShow.length; ++i) {
                    featuresToShow[i].properties.icon = iconsMapping[featuresToShow[i].properties.incidentCategory];
                    featuresToShow[i].properties.formattedLength = formatLength(featuresToShow[i]
                        .properties.lengthMeters);
                }
                marker.bindPopup(clusterPopupTemplate
                    .render({
                        features: featuresToShow,
                        incidentsNo: feature.properties.features.length
                    }), {autoPan: false});
            }
        } else {
            iconOptions.content = null;
            iconOptions.icon.icon = 'icon-' + iconsMapping[feature.properties.incidentCategory] + '-white';
            marker = L.marker(latlng, {icon: L.svgIcon(iconOptions)});
            marker.bindPopup(trafficIncidentMessage(feature.properties, imperialDistance),
                {className: 'incidents-popup'});
        }
        return marker;
    };
});



