/**
 * Base class for all the Search services.
 *
 * @private
 * @class Search
 * @module Services
 * @uses Chain
 * @uses ProtocolMixin
 * @uses KeyMixin
 * @uses LimitMixin
 * @uses LanguageMixin
 * @uses UnwrapMixin
 *
 * @constructor
 * @param {Object}   [options]
 * @param {String}   [options.key] An API Key issued from <a href="http://developer.tomtom.com">Developer Portal</a>
 * @param {"http"|"https"} [options.protocol="https"] Protocol's type
 * @param {Boolean}  [options.unwrapBbox=false] Changes the bounding box coordinates allowing longitudes that
 *  exceeds 180 degrees when is necessary. {{#crossLink "Search/unwrapBbox:method"}}More{{/crossLink}}
 * @param {Number}   [options.limit=10] Maximum number of responses that will be returned
 * @param {String}   [options.language=null] Language to use in the response.
 *  The list with all the available codes can be found
 *  <a href="https://developer.tomtom.com/online-search/online-search-documentation/supported-languages">here</a>.
 * @param {Function} [options.callback] Callback function to use in case of success
 * @param {Function} [options.fail=default] Callback function to use in case of failure
 */

define(['rest/search', 'model/search/search', '../chain', '../validators', '../converters', '../worldEdge', '../utils'],
    function(search, model, chain, validators, converters, worldEdge, utils) {
        'use strict';

        var TYPES = {
                search: 'search',
                poi: 'poiSearch',
                category: 'categorySearch',
                geometry: 'geometrySearch',
                nearby: 'nearbySearch',
                lowBandwidth: 's'
            }, fields = {
                key: {validator: validators.string},
                query: {validator: validators.string, converter: converters.queryString, required: true},
                typeahead: {validator: validators.bool},
                limit: {validator: validators.limit},
                offset: {validator: validators.offset, name: 'ofs'},
                language: {validator: validators.languageCode, converter: converters.language},
                countrySet: {validator: validators.countrySet},
                radius: {validator: validators.positiveInteger},
                boundingBox: {validator: validators.boundingBox, converter: converters.boundingBox},
                center: {converter: converters.point, validator: validators.point},
                type: {validator: validators.string, defaultValue: TYPES.search},
                bestResult: {validator: validators.bool},
                protocol: {validator: validators.protocol}, //FIXME: What happen with this validator?
                extendedPostalCodesFor: { validator: validators.string },
                unwrapBbox: {validator: validators.bool, defaultValue: false, required: true}
            };

        function addBoundingBoxParameters(data, boundingBox) {
            data.topLeft = [boundingBox.maxLat, boundingBox.minLon];
            data.btmRight = [boundingBox.minLat, boundingBox.maxLon];
            return data;
        }

        function callbackBestResult(bestResult, callback) {
            return function(response) {
                callback(bestResult ? response[0] : response);
            };
        }

        function Search(options) {
            this.options = options;
            this.defaultFields = utils.clone(fields);
            this.fields = {};
        }

        Search.prototype.TYPE = TYPES;
        Search.prototype.model = model;
        Search.prototype.construct = function() {
            var mergedFields = utils.addFields.call(this, this.fields);
            this.options = this.options || {};
            this.options.__serviceType = 'search';
            return chain.call(this, mergedFields, this.handleChain, this.options);
        };

        Search.prototype.handleChain = function(data, callback) {
            var bestResult = Boolean(data.bestResult);
            if (bestResult) {
                data.limit = 1;
                data.offset = 0;
            }

            if (data.center) {
                data.center = data.center.split(',');
                data.lat = data.center[0];
                data.lon = data.center[1];
                delete data.center;
            }
            worldEdge(search.search, this.model, data, callbackBestResult(bestResult, callback),
                addBoundingBoxParameters);
        };
        //TODO We should think how to refactor this, code below does some WEIRD trick.
        //Take a look in fuzzySearch.js how it is called - they call just constructor function
        function searchConstructor(options) {
            return (new Search(options)).construct();
        }

        searchConstructor.prototype.constructor = Search;

        return searchConstructor;
    });



