define(['http', './utils', 'security', 'endpoints', '../requester/template', '../services/routing/routingParameters'],
    function(http, utils, security, endpoints, template, routingParameters) {
        'use strict';

        var queryParamNames = Object.keys(routingParameters(['avoid', 'vehicleLoadType', 'locations']));

        function isEmpty(values) {
            return !values || !values.length;
        }

        function getMultipleParamQueryString(values, name, firstParam) {
            if (isEmpty(values)) {
                return '';
            }
            return (firstParam ? '?' : '&') + name + '=' + values.join('&' + name + '=');
        }

        function encodeQueryData(data) {
            var ret = [], d;
            for (d in data) {
                if (data.hasOwnProperty(d)) {
                    ret.push(encodeURIComponent(d) + '=' + encodeURIComponent(data[d]));
                }
            }
            return ret.join('&');
        }

        return {
            calculateRoute: function(options, callback) {
                var pathParams = {},
                    queryParams = {},
                    queryStringAvoid,
                    queryStringVehicleLoadType;

                queryParams.key = utils.or(options.key, security.getKey('services/routing'));

                pathParams.locations = utils.required(options.locations);

                // language
                queryParams.language = utils.or(options.language, 'en-GB');

                // look for not required passed params and set them if they are not undefined
                utils.extractQueryParams(options, queryParams, queryParamNames);

                if (queryParams.vehicleEngineType === 'electric') {
                    if (queryParams.constantSpeedConsumptionInkWhPerHundredkm) {
                        queryParams.constantSpeedConsumptionInkWhPerHundredkm =
                            options.constantSpeedConsumptionInkWhPerHundredkm.join(':');
                    }
                } else if (queryParams.constantSpeedConsumptionInLitersPerHundredkm) {
                    queryParams.constantSpeedConsumptionInLitersPerHundredkm =
                        options.constantSpeedConsumptionInLitersPerHundredkm.join(':');
                }

                queryStringAvoid = getMultipleParamQueryString(options.avoid, 'avoid', true);
                queryStringVehicleLoadType = getMultipleParamQueryString(options.vehicleLoadType, 'vehicleLoadType',
                    !queryStringAvoid);

                http.get({
                    url: '{protocol}://' + endpoints.routing + queryStringAvoid + queryStringVehicleLoadType,
                    pathParameters: pathParams,
                    queryParameters: queryParams,
                    callbackParam: 'callback',
                    callback: callback,
                    fail: options.fail
                });
            },

            calculateReachableRange: function(options, callback) {

                var pathParams = {},
                    queryParams = {},
                    queryStringAvoid,
                    queryStringVehicleLoadType,
                    calculateRouteQueryParamNames = ['fuelBudgetInLiters', 'energyBudgetInkWh'];

                queryParams.key = utils.or(options.key, security.getKey('services/routing'));

                pathParams.origin = utils.required(options.origin);

                // look for not required passed params and set them if they are not undefined
                utils.extractQueryParams(options, queryParams, calculateRouteQueryParamNames.concat(queryParamNames));

                if (queryParams.vehicleEngineType === 'electric') {
                    queryParams.constantSpeedConsumptionInkWhPerHundredkm =
                        options.constantSpeedConsumptionInkWhPerHundredkm.join(':');
                } else {
                    queryParams.constantSpeedConsumptionInLitersPerHundredkm =
                        options.constantSpeedConsumptionInLitersPerHundredkm.join(':');
                }

                queryStringAvoid = getMultipleParamQueryString(options.avoid, 'avoid', true);
                queryStringVehicleLoadType = getMultipleParamQueryString(options.vehicleLoadType, 'vehicleLoadType',
                    !queryStringAvoid);

                http.get({
                    url: '{protocol}://' + endpoints.reachableRange + queryStringAvoid + queryStringVehicleLoadType,
                    pathParameters: pathParams,
                    queryParameters: queryParams,
                    callbackParam: 'callback',
                    callback: callback,
                    fail: options.fail
                });
            },

            batch: function(options, callback) {
                var queryParams = {},
                    bodyParams = {},
                    rawBatchItems;

                queryParams.key = utils.or(options.key, security.getKey('services/routing'));

                rawBatchItems = utils.required(options.batchItems);

                bodyParams.batchItems = rawBatchItems.map(function(item) {
                    var pathParams = {}, itemParams = {}, query;
                    pathParams.locations = utils.required(item.locations);
                    pathParams.contentType = 'json';
                    utils.extractQueryParams(item, itemParams, queryParamNames);
                    itemParams.language = utils.or(item.language, options.language || 'en-GB');

                    query = template(endpoints.batchRoutingQuery, pathParams);
                    query += encodeQueryData(itemParams);
                    query += getMultipleParamQueryString(options.avoid, 'avoid', false);
                    query += getMultipleParamQueryString(options.vehicleLoadType, 'vehicleLoadType', false);

                    return {query: query};
                });

                http.post({
                    url: '{protocol}://' + endpoints.batchRouting,
                    queryParameters: queryParams,
                    bodyParameters: bodyParams,
                    callbackParam: 'callback',
                    callback: callback,
                    fail: options.fail
                });
            },

            matrix: function(options, callback) {
                var queryParams = {},
                    bodyParams = {};
                queryParams.key = utils.or(options.key, security.getKey('services/routing'));

                utils.extractQueryParams(options, queryParams, queryParamNames);

                bodyParams.origins = utils.required(options.origins);
                bodyParams.destinations = utils.required(options.destinations);

                http.post({
                    url: '{protocol}://' + endpoints.matrixRouting,
                    queryParameters: queryParams,
                    bodyParameters: bodyParams,
                    callbackParam: 'callback',
                    callback: callback,
                    fail: options.fail
                });
            }
        };
    });



