define([], function() {
    'use strict';

    return function(data) {
        if (typeof data === 'string') {
            try {
                data = JSON.parse(data); // aja js has problems and response is not always parsed as it should
            } catch (e) {
                return null;
            }
        }
        if (data.error) {
            return { error: data.error};
        } else if (data.matrix) {
            return data.matrix.map(function(row) {
                return row.map(function(cell) {
                    return cell.response;
                });
            });
        }
        return null;
    };
});



