/**
 * Tile layer for Leaflet that shows styled traffic tubes using
 * <a href="https://developer.tomtom.com/online-traffic">online traffic data</a>
 * from TomTom.
 *
 * @class TomTomTrafficLayer
 * @extends L.TileLayer
 * @module Map
 * @namespace L
 * @constructor
 * @param {Object} [options] It accepts all the options that
 *     <a href="http://leafletjs.com/reference-${leaflet.version}.html#tilelayer-options">Leaflet's TileLayer</a>
 *     allows.
 *     The additional options, which are TomTom specific, are described below.
 * @param {String} [options.key] API key that can be obtained from
 *     <a href="http://developer.tomtom.com">TomTom's developer portal</a>.
 *     This is a key for the traffic services.
 * @param {"s1"|"s2"|"s3"|"night"} [options.style="night"] The traffic layer style to use
 * @param {Number} [options.opacity=0.8] The traffic incidents layer's opacity set by default to 80%.
 *     A floating point value between `0` and `1` have to be used, where `1` means no transparency.
 */

/**
 * This is an extension to Leaflet allowing to display the TomTom traffic incidents layer.
 *
 * It uses the tomtom.trafficIncidents service in order to retrieve incidents details.
 *
 * A look and feel, together with a clustering behaviour, can be modified by passing the pointToLayer function. The
 * default implementation looks as follows
 * ```javascript
 *
 * define(['leaflet', 't', 'unitFormatConverter/unitFormatConverter'], function (L, T, units) {
 *   'use strict';
 *
 *   var iconsMapping = {
 *           'Unknown': 'danger', 'Accident': 'accident', 'Fog': 'danger', 'Dangerous Conditions':
 *           'danger', 'Rain': 'danger', 'Ice': 'ice', 'Jam': 'incident', 'Lane Closed':
 *           'laneclosed', 'Road Closed': 'roadclosed', 'Road Works': 'roadwork', 'Wind': 'danger',
 *           'Flooding': 'danger', 'Detour': 'danger', 'Cluster': ''
 *       },
 *       iconTemplate = '<div class="traffic-icon"><div class="icon-circle-{{=incidentSeverity}}">' +
 *           '<div class="icon-{{=icon}}-white"></div></div></div>',
 *       shieldTemplate = '{{roadNumber}}<div class="road-shield">{{=roadNumber}}</div>' +
 *       '{{/roadNumber}}',
 *       descriptionTemplate = '<div class="traffic-description"><div class="incident-category">' +
 *       '{{=incidentCategory}}</div><div class="incident-delay-length"{{!delaySeconds}}' +
 *       '{{!lengthMeters}} style="display:none;"{{/!lengthMeters}}{{/!delaySeconds}}>' +
 *       '{{delaySeconds}}<div class="incident-delay">{{=time}}</div>{{/delaySeconds}}' +
 *       '{{lengthMeters}}<div class="incident-length">{{=distance}}</div>{{/lengthMeters}}</div>' +
 *       '</div>',
 *       messageTemplate = new T('<div class="traffic-details">' + iconTemplate + shieldTemplate +
 *       descriptionTemplate + '</div>');
 *
 *   function trafficIncidentMessage(info, imperialDistance) {
 *       info.distance = units.formatDistance(info.lengthMeters, imperialDistance);
 *       info.time = units.formatTime(info.delaySeconds);
 *       info.incidentSeverity = info.incidentSeverity || 'unknown';
 *       info.icon = iconsMapping[info.incidentCategory];
 *       return messageTemplate.render(info);
 *   }
 *
 *   function clusterClickHandler(bounds, latlng) {
 *       if (bounds) {
 *           return function () {
 *               this._map.fitBounds(bounds);
 *           };
 *       } else {
 *           return function () {
 *               this._map.setZoomAround(latlng, this._map.getZoom() + 2);
 *           };
 *       }
 *   }
 *
 *   return function (feature, latlng, imperialDistance) {
 *       var iconOptions = {
 *           background: {
 *               iconSize: L.Browser.retina ? [34, 34] : [20, 20],
 *               iconAnchor: L.Browser.retina ? [17, 17] : [10, 10],
 *               icon: ''
 *           },
 *           icon: {
 *               iconSize: L.Browser.retina ? [34, 34] : [20, 20],
 *               iconAnchor: [0, 0],
 *               icon: ''
 *           },
 *           content: {
 *               iconSize: L.Browser.retina ? [34, 34] : [20, 20],
 *               iconAnchor: [0, 0],
 *               content: '',
 *               'style': {
 *                   'color': '#fff',
 *                   'font-weight': 'bold',
 *                   'font-size': L.Browser.retina ? '18px' : '11px'
 *               }
 *           },
 *           popupAnchor: L.Browser.retina ? [0, -8] : [0, -5]
 *       }, marker;
 *
 *       if (!feature || !feature.properties) {
 *           return null;
 *       }
 *
 *       iconOptions.background.icon = 'icon-circle-' +
 *           (feature.properties.incidentSeverity || 'unknown');
 *
 *       if (feature.properties.clusterSize) {
 *           iconOptions.icon = null;
 *           iconOptions.content.content = feature.properties.clusterSize < 100
 *              ? feature.properties.clusterSize:'99+';
 *           marker = L.marker(latlng, {icon: L.svgIcon(iconOptions)}); marker.on('click',
 *               clusterClickHandler(feature.properties.clusterBounds, latlng));
 *       } else {
 *           iconOptions.content = null;
 *           iconOptions.icon.icon = 'icon-' + iconsMapping[feature.properties.incidentCategory] +
  *              '-white';
 *           marker = L.marker(latlng, {icon: L.svgIcon(iconOptions)});
 *           marker.bindPopup(trafficIncidentMessage(feature.properties, imperialDistance),
 *              {className: 'incidents-popup'});
 *       }
 *       return marker;
 *   };
 * });
 * ```
 *
 * @class TomTomTrafficIncidentsLayer
 * @extends L.GeoJSON
 * @namespace L
 * @module Map
 * @constructor
 * @param {Object} [options] It accepts all the options that
 *     <a href="http://leafletjs.com/reference-${leaflet.version}.html#geojson-options">Leaflet's GeoJSON</a>
 *     class allows.
 *     The additional options, which are TomTom specific, are described below.
 * @param {String} [options.key] API key that can be obtained from
 *     <a href="http://developer.tomtom.com">TomTom's developer portal</a>.
 *     This is a key for the traffic services.
 * @param {Function} [options.pointToLayer=default] This function is used to convert vector traffic
 *     incidents into a layer of markers on the map. It takes the GeoJSON feature as the first parameter and the latlng
 *     object as the second one. It returns a layer that will be added to the map. If this is omitted, the default
 *     implementation is used (displayed above).
 * @param {Boolean} [options.imperialDistance=null] Set this option if you want to use a specific unit measurement
 *     system for the new instance of the layer instead of use the default unit
 * @param {Boolean} [options.createPopupForCluster=false] Set this to true if you want popup for clusters to be
 *     generated
 */
define(['leaflet', 'security', 'services/trafficModelId', 'services/trafficIncidents', './defaultIncidentsLayer',
    'http', 'endpoints', '../../config/unit/unitTypes'],
function(L, security, trafficModelId, trafficIncidents, defaultPointToLayer, http, endpoints, unitTypes) {
    'use strict';

    var trafficStyles = ['s1', 's2', 's3', 'night'];

    function getTrafficStyle(trafficLayer) {
        var style = 'night', i;
        if (typeof trafficLayer === 'string') {
            for (i = 0; i < trafficStyles.length; i += 1) {
                if (trafficStyles[i] === trafficLayer.toLowerCase()) {
                    return trafficStyles[i];
                }
            }
        }
        return style;
    }

    function createPointToLayerHandler(layer) {
        return function(feature, latlng) {
            return defaultPointToLayer(feature, latlng, layer._shouldUseImperialDistance(), layer.options);
        };
    }

    /**
     * @ignore
     * In order to differentiate markers to remove when traffic data updates. This flag is added.
     * @param {Function} pointToLayer
     * @return {Function}
     */
    function wrapPointToLayer(pointToLayer) {
        return function(feature, latlng) {
            var marker = pointToLayer(feature, latlng);
            marker.__isTrafficMarker = true;
            return marker;
        };
    }

    L.TomTomTrafficIncidentsLayer = L.GeoJSON.extend({
        options: {
            imperialDistance: null,
            trafficModelId: -1,
            minZoom: 7
        },
        name: 'TomTomTrafficIncidentsLayer',
        /**
         * @ignore
         * First check the configuration for this instance and then the global configuration.
         * The instance configuration has priority.
         *
         * @return {Boolean}
         */
        _shouldUseImperialDistance: function() {
            if (typeof this.options.imperialDistance === 'boolean') {
                return this.options.imperialDistance;
            }
            return this.unitService.getCurrentUnit().getLabel() === unitTypes.imperial;
        },
        initialize: function(options) {
            L.Util.setOptions(this, options);
            this._layers = {};
            security.setKey('services/traffic', options.key);
            this.incidentsService = trafficIncidents(options.trafficService)
                .key(security.getKey('services/traffic'))
                .style(getTrafficStyle(options.style));
            this.options.pointToLayer = wrapPointToLayer(this.options.pointToLayer || createPointToLayerHandler(this));
        },
        onAdd: function(map) {
            this._map = map;
            this.unitService = map.getUnitService();
            this.localeService = map.getLocaleService();
            this.eachLayer(map.addLayer, map);
            map.on({'viewreset': this.redraw, 'moveend': this.update}, this);
            this.unitService.on(L.UnitService.Events.UNIT_CHANGED, this.redraw, this);
            this.localeService.on(L.LocaleService.Events.LOCALE_CHANGED, this.redraw, this);
        },

        onRemove: function(map) {
            this.unitService.off(L.UnitService.Events.UNIT_CHANGED, this.redraw, this);
            map.off({'viewreset': this.redraw, 'moveend': this.update}, this);
        },
        _getIncidentsServiceForRedrawOrUpdate: function() {
            var map = this._map, zoom;
            if (!map) {
                return null;
            }
            zoom = map.getZoom();
            if (zoom < this.options.minZoom) {
                this.clearLayers();
                return null;
            }
            return this.incidentsService
                .boundingBox(map.getBounds())
                .zoomLevel(zoom)
                .language(this.localeService.getCurrentLocale().getValueFor('traffic'));
        },
        redraw: function() {
            var that = this;
            var preconfiguredIncidentsService = this._getIncidentsServiceForRedrawOrUpdate();
            if (!preconfiguredIncidentsService) {
                return;
            }

            preconfiguredIncidentsService.trafficModelID(this.options.trafficModelId)
                .go()
                .then(function(geoJSON) {
                    that.clearLayers().addData(geoJSON);
                    that.fire(L.TomTomTrafficIncidentsLayer.Events.TRAFFIC_INCIDENTS_RECEIVED, {
                        data: geoJSON,
                        layers: Object.keys(that._layers).map(function(key) {
                            return that._layers[key];
                        })
                    });
                });
        },
        update: function() {
            var that = this;
            var preconfiguredIncidentsService = this._getIncidentsServiceForRedrawOrUpdate();
            if (!preconfiguredIncidentsService) {
                return;
            }
            preconfiguredIncidentsService.go()
                .then(function(geoJSON) {
                    /*
                     * Incidents service returns incidents with its coordinates
                     * It is important to remove old markers
                     */
                    that.eachLayer(function(layer) {
                        if (layer.__isTrafficMarker) {
                            that.removeLayer(layer);
                        }
                    });
                    that.addData(geoJSON);
                    that.fire(L.TomTomTrafficIncidentsLayer.Events.TRAFFIC_INCIDENTS_RECEIVED, {
                        data: geoJSON,
                        layers: Object.keys(that._layers).map(function(key) {
                            return that._layers[key];
                        })
                    });
                });
        },
        setTrafficModelId: function(trafficModelId) {
            if (this.options.trafficModelId !== trafficModelId) {
                this.options.trafficModelId = trafficModelId;
                this.redraw();
            }
        }
    });

    L.tomTomTrafficIncidentsLayer = function(options) {
        return new L.TomTomTrafficIncidentsLayer(options);
    };

    /**
     * Fired when new incidents data is received.
     *
     * @event L.TomTomTrafficIncidentsLayer.Events.TRAFFIC_INCIDENTS_RECEIVED
     * @param {Object} data GeoJson data
     * @param {Array} layers
     */
    L.TomTomTrafficIncidentsLayer.Events = {
        TRAFFIC_INCIDENTS_RECEIVED: 'tomtom.trafficincidentsreceived'
    };

    function handleSubdomains(options) {
        if (typeof options.subdomains === 'string') {
            options.subdomains = options.subdomains.split('');
        }
    }

    function loadAnimation() {
        var opacity = 'opacity',
            back = this._bgBuffer;
        back.style[L.DomUtil.TRANSITION] = opacity + ' 10';
        back.style[opacity] = 0.1;
        this._tileContainer.style[opacity] = this.options.opacity;
    }

    L.TomTomTrafficLayer = L.TileLayer.extend({
        options: {
            attribution: '',
            minZoom: 2,
            maxZoom: 18,
            subdomains: 'abcd',
            reuseTiles: true,
            opacity: 0.8,
            errorTileUrl: 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAQAAAAEAAQMAAABmvDolAAAAA1BMVEUAAACnej3aAAAAAXRSTlMAQObYZgAAAB9JREFUGBntwQENAAAAwiD7p34ON2AAAAAAAAAAAOcCIQAAAfWivQQAAAAASUVORK5CYII=', //eslint-disable-line
            detectRetina: false,
            trafficModelId: -1
        },
        name: 'traffic',
        initialize: function(options) {
            var key;
            options = L.setOptions(this, options);
            if (options.bounds) {
                options.bounds = L.latLngBounds(options.bounds);
            }
            security.setKey('services/traffic', options.key);
            key = security.getKey('services/traffic');
            options.accessToken = key;
            this._url = http.protocol() + '://' + endpoints.trafficLayer +
                '?t={trafficModelId}' + (key ? '&key={accessToken}' : '');
            options.style = getTrafficStyle(options.style);
            handleSubdomains(options);
            options.tileSize = options.detectRetina && L.Browser.retina ? 512 : 256;
            if (options.tileSize === 512) {
                options.zoomOffset -= 1;
                options.minZoom = options.minZoom > 0 ? options.minZoom - 1 : 0;
            }
            this.setOpacity(options.opacity);
        },
        onAdd: function(map) {
            L.TileLayer.prototype.onAdd.call(this, map);
            if (this._animated) {
                this.on('load', loadAnimation);
            }
            this._updateOpacity();
        },
        setTrafficModelId: function(trafficModelId) {
            if (this.options.trafficModelId !== trafficModelId) {
                this.options.trafficModelId = trafficModelId;
                this.redraw();
            }
        }
    });

    L.tomTomTrafficLayer = function(options) {
        return new L.TomTomTrafficLayer(options);
    };

    return function(options) {
        var raster = L.tomTomTrafficLayer(options),
            incidents = L.tomTomTrafficIncidentsLayer(options),
            trafficModelIdService = trafficModelId({key: security.getKey('services/traffic')});

        function refreshTrafficModelId() {
            trafficModelIdService.go().then(function(trafficModelId) {
                raster.setTrafficModelId(trafficModelId);
                incidents.setTrafficModelId(trafficModelId);
            });
        }

        refreshTrafficModelId();
        // refresh the traffic layer
        if (!options.noRefresh) {
            setInterval(refreshTrafficModelId, 30000);
        }
        return L.layerGroup([raster, incidents]);
    };
});



