/**
 * Service for store and retrieve an specific geopolitical view.
 *
 * This service is used by map instances, widgets and services to store their geopolitical
 * view configuration.
 *
 * If its constructor receive a GeopolViewService instance as argument it will start listen changes in the
 * given instance and apply the same change in its own. That behavior allows the instances of this class to
 * be updated in cascade.
 *
 * @class GeopolViewService
 * @module Services
 * @extends L.Evented
 * @constructor
 * @param {GeopolViewService} [parent] If is defined makes the new instance a "slave" of the given instance
 */
define(['./geopolView', 'leaflet', './geopolViewsMapping', 'services/utils'],
    function(GeopolView, L, geopolViewsMapping, Utils) {
        'use strict';
        var GeopolViewService;
        var defaultGeopolViewCode = 'Unified';
        var EVENTS = {
            /**
             * Fired after the view was changed.
             * @event GeopolViewService.Events.GEOPOL_VIEW_CHANGED
             */
            'GEOPOL_VIEW_CHANGED': 'config.geopolView.changed'
        };
        var supportedViews = Object.keys(geopolViewsMapping).map(function(key) {
            return new GeopolView(key);
        });

        /**
         * @ignore
         * @return {GeopolView}
         */
        function getDefaultGeopolView() {
            return new GeopolView(defaultGeopolViewCode);
        }

        GeopolViewService = L.Evented.extend({

            initialize: function(parent) {
                if (parent instanceof GeopolViewService) {
                    this.current = new GeopolView(parent.getCurrentGeopolView().getCode());
                    this._enslaveTo(parent);
                } else {
                    this.current = getDefaultGeopolView();
                }
            },

            /**
             * Sets the current geopolitical view.
             *
             * It also fires the event
             * {{#crossLink "GeopolViewService/GeopolViewService.Events.GEOPOL_VIEW_CHANGED:event"}}{{/crossLink}}.
             *
             * @method setCurrentGeopolView
             * @param {String|GeopolView} [geopolView='Unified'] view code or a GeopolView instance
             */
            setCurrentGeopolView: function(geopolView) {
                if (typeof geopolView === 'string') {
                    geopolView = new GeopolView(geopolView);
                }
                if (this.current.getCode() !== geopolView.getCode()) {
                    this.current = new GeopolView(geopolView.getCode());
                    this.fire(EVENTS.GEOPOL_VIEW_CHANGED, this.current);
                }
            },

            /**
             * Returns the current geopolitical view stored in the instance.
             *
             * @method getCurrentGeopolView
             * @return {GeopolView}
             */
            getCurrentGeopolView: function() {
                return this.current;
            },

            cleanEvents: function() {
                if (this.parent) {
                    this.parent.off(EVENTS.GEOPOL_VIEW_CHANGED, this.setCurrentGeopolView, this);
                    this.parent = undefined;
                }
                this.off();
            },

            /**
             * Make this instance a slave of another GeopolViewService instance.
             * What it basically means is that this instance will copy the current
             * geopolitical view of the parent instance when it changes.
             *
             * @ignore
             * @param {GeopolViewService} parent
             */
            _enslaveTo: function(parent) {
                this.parent = parent;
                parent.on(EVENTS.GEOPOL_VIEW_CHANGED, this.setCurrentGeopolView, this);
            }
        });

        /**
         * Static method that returns an array with the supported geopolitical views.
         *
         * @static
         * @method GeopolViewService.getSupportedViews
         * @return {GeopolView[]}
         */
        GeopolViewService.getSupportedViews = function() {
            return supportedViews;
        };

        GeopolViewService.Events = EVENTS;

        Utils.addDeprecatedProperty(GeopolViewService, 'EVENTS', 'Events');

        /**
         * Leaflet factory of this service.
         *
         * @param {GeopolViewService} parent
         * @return {GeopolViewService}
         */
        L.geopolViewService = function(parent) {
            return new GeopolViewService(parent);
        };

        L.GeopolViewService = GeopolViewService;

        return L.GeopolViewService;
    });



